<template>
    <input class="form_input" :value="value" @input="chageData($event)" v-bind="$attrs" />
</template>

<script>
export default {
    name: 'yc_form_input',
    data(){
        return {
            
        }
    },
    props:{
        value:{
          type:String
        },
    },
    created(){
      
    },
    mounted(){
      
    },
    methods:{
        chageData:function(e){
          this.$emit('input',e.target.value);
          // this.$emit('');//直接让父级校验，不行，父级是<slot>标签上不能绑定事件
          this.$parent.$emit('validate');
        }
    }
}

</script>

