<template>
    <div :class="{'yc_canvasToImg':true,'yc_canvasToImg_show':isGetImg}" v-show="false">
        <canvas class="yc_canvasToImg_canvas" v-bind="$attrs" />
        <a ref="yc_canvasToImg_a" :download="imgs.name" :href="imgs.url"></a>
    </div>
</template>

<script>
export default {
    name: 'yc_canvasToImg',
    data(){
        return {
            imgs:{url:'',name:''}
        }
    },
    props:{
        content:{
            type:Array,
            default:[]
        },
        isGetImg:{
            type:Boolean,
            default:false
        },
        after_getImg:{
            type:Function,
            default:function(){}
        }
    },
    model:{
        prop:'isGetImg',
        event:'resolveGetImg'
    },
    watch:{
        isGetImg:{
            handler(newVal){
                if(newVal){ 
                    this.initCanvas();  
                }
            },
            immediate:true
        }
    },
    methods:{
        initCanvas(){
            var canvas = document.getElementsByClassName('yc_canvasToImg_canvas')[0];
            var ctx = canvas.getContext('2d');
            
            this.content.sort((a,b)=>{
                return b.zIndex-a.zIndex;
            })
            this.drawImage(ctx,this.content,0);
        },
        drawImage(ctx,content,idx){
            if(idx==content.length){
                this.downImg();
                return;
            }
            var that = this;

            if(content[idx].type=='text'){
                ctx.fillStyle = content[idx].fillStyle ? content[idx].fillStyle : 'black';
                ctx.font = content[idx].font;
                ctx.fillText(content[idx].text,content[idx].x,content[idx].y);
                this.drawImage(ctx,content,idx+1);
            }else if(content[idx].type=='img'){
                var imgTemp = new Image();
                imgTemp.src = content[idx].url;
                imgTemp.setAttribute("crossOrigin",'anonymous');//图片跨域
                
                imgTemp.onload = function(){   //图片加载完后执行
                    ctx.drawImage(imgTemp,content[idx].x,content[idx].y,content[idx].width,content[idx].height);
                    that.drawImage(ctx,content,idx+1);
                }
            }else{   //类型不是text和img
                this.drawImage(ctx,content,idx+1);
            }
        },
        downImg(){
            var canvas = document.getElementsByClassName('yc_canvasToImg_canvas')[0];
            var imgUrl = canvas.toDataURL("image/png");
            this.imgs = {
                url:imgUrl,
                name:'test'
            }
            //结束
            this.$nextTick(()=>{
                this.$refs.yc_canvasToImg_a.click();
                this.$emit('resolveGetImg',false);
                this.after_getImg(imgUrl);
            });
        }
    }
}

</script>

<style scoped >
.yc_canvasToImg_show{
    position:fixed;
    z-index:-100;
}
</style>
