<template>
    <div class="yc_backTo"  @click="goTop">
        <div class="yc_backTo_icon"></div>
    </div>
</template>

<script>
export default {
    name:'yc_backTo',
    data(){
        return{
            scrollTop:0,
            isScrolling:false,
        }
    },
    methods:{
        goTop(){
            if(this.isScrolling){
                return;
            }
            this.goTopFn();
        },
        goTopFn(){
            var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            var that = this;
            
            if (scrollTop > 0) {
                var scrollTop = scrollTop - scrollTop / 8;

                window.scrollTo( 0 , scrollTop );
                window.requestAnimationFrame(that.goTopFn)
                this.isScrolling = true;
            }else{
                this.isScrolling = false;
            }
        }
    }
}
</script>

<style>
.yc_backTo{
    position:fixed;
    z-index:200;
    right:20px;
    bottom:80px;
    width:100px;
    height:100px;
    border-radius: 50%;
    background-color:#fff;
    box-shadow: 0 0 6px rgb(0 0 0 / 12%);
}
.yc_backTo_icon{
    position:absolute;
    width:0;
    height:0;
    border-top:25px solid transparent;
    border-left:25px solid transparent;
    border-right:25px solid transparent;
    border-bottom:25px solid #409eff;
    transform: translate(50%,25%);
}
</style>