<template>
  <div class="yc_grid_item" :style="{width:itemWidth.itemWidth}" @touchstart.passive="addCss" @touchend.passive="removeCss">
        <div class="grid_item">
            <template v-if="$slots.default">
                <slot />
            </template>
            <template v-else>
                <div class="grid_item_box">
                    <template v-if="icon">
                        <img class="grid_item_box_img" :src="icon" alt="" v-if="icon.slice(0,4)=='http' ">
                        <i :class="['iconfont',icon]" v-else></i>
                    </template>
                </div>
                <p class="grid_item_txt">{{text}}</p>
            </template>
        </div>
  </div>
</template>

<script>
export default {
    name: 'yc_grid_item',
    props: {
        text:{
            default:()=>{}
        },
        icon:{
            type: String,
            default:()=>{}
        },
        idx:{
            type:Number,
        }
    },
    inject:['itemWidth'],
    methods:{
        addCss:function(){
            var dom = document.querySelectorAll(".yc_grid_item")[this.idx];
            dom.style.backgroundColor = '#eaebec';
        },
        removeCss:function(){
            var dom = document.querySelectorAll(".yc_grid_item")[this.idx];
            dom.style.backgroundColor = '';
        }
    }
}


</script>

<style scoped >
.yc_grid_item{
    overflow: hidden;
    background-color:#fff;
}
.grid_item{
    padding:32px 16px;
}
.grid_item_box{
    display: flex;
    overflow: hidden;
    height:56px;
    justify-content: center;
    align-items: center;
}
.grid_item_box_img{
    display:block;
    width:56px;
    height:56px;
}
.iconfont{
    font-size:56px;
}
.grid_item_txt{
    margin-top:16px;
    color: #646566;
    font-size: 24px;
    word-wrap: break-word;
    text-align:center;
}
</style>
