<template>
  <div class="yc_form_item">
    <span class="item_label">{{label}}</span>
    <slot />
  </div>
</template>

<script>
export default {
    name: 'yc_form_item',
    inject:['form'],
    data(){
        return {
            
        }
    },
    props:{
      label:{
        type:String,
      },
      prop:{
        type:String,
      }
    },
    created(){
      
    },
    mounted(){
      this.$on('validate',()=>{
        this.validate();
      })
    },
    methods:{
      validate(){
        var formData = this.form.formData;
        var rules = this.form.rules;
        var prop = this.prop;

        var result=true;
        for(var i=0,len=rules.length;i<len;i++){
          if(rules[i].prop==prop){
            var temp=true;
            var val = rules[i].model;
            for(var j=0,len=val.length;j<len;j++){
              if(typeof val[j].type == "string"){
                var reg = new RegExp(val[j].type);
                console.log(reg,formData[prop])
                temp = reg.test(formData[prop]);
              }else if(typeof val[j].type == "number"){
                if(val[j].type==1){
                  temp = !!formData[prop];
                }
              }
              console.log(result)
              if(!temp){break;}
            }
            return result;
          }
        }
        console.log(result)
        return result;
      }
    }
}

</script>

