        {
          prop:"password",
          model:[
            {type:'^\d+$',errTxt:'只能包含字母数字下画线'},   //type支持数字或正则： 1不能为空；规则有交叉，比如满足正则的其必然不为空？
            {type:1,errTxt:'不能为空'} 
          ], 
