      inserted: function(el,parps){
        if(parps.value){
          el.addEventListener('touchstart',function(){
            let colorOrigin = el.style.color;
            el.style.color = parps.value;
            el.addEventListener('touchend',function(){
              el.style.color = colorOrigin ? colorOrigin : "";
            })
