      var obody = document.querySelector("body");

      oInput.value = txt;
      obody.append(oInput);

      try{
        oInput.select();
        document.execCommand('Copy',"false",null);
      }catch(err){
        alert("复制错误,请手动复制");
      }
      oInput.style.visibility = "hidden";
