<template>
    <transition name="yc_popup">
        <div class="yc_popup"  
            @touchmove.prevent.stop  
            @touchstart.prevent.stop="closePopup" 
            v-show="showPopup"
        >
            <transition :name="getClassName()">
                <div 
                    :class="['popup_cover',getClassName()]" 
                    @touchstart.stop 
                    v-show="showPopup"
                    :style="{...contStyle}"
                >
                    <slot />
                </div> 
            </transition>
            <transition name="yc_cover_fade">
                <!-- tranition组件外如果有判断，用v-show不能v-if -->
                <div class="yc_popup_cover" v-show="showPopup"></div>
            </transition>
        </div>
    </transition>
</template>

<script>
export default {
    name: 'yc_popup',
    data(){
        return {
            moveX:0,
            moveY:0
        }
    },
    model:{
        prop:'showPopup',
        event:'changeShow'
    },
    props:{
        showPopup:{
            type:Boolean,
            default:false
        },
        direction:{
            type:String,
            default:'bottom'
        },
        contStyle:{
            type:Object,
            default:{}
        },
        close:{
            type:Function,
            default:()=>{}
        }
    },
    mounted(){
        
    },
    methods:{
        closePopup(){
            this.$emit('changeShow',false);
            this.$emit('close');
        },   
        getClassName(){
            let direction = this.direction;

            if(direction=='left'){
                this.moveX = 0;
                this.moveY = 0;
                return 'popup_cover_left';
            }else if(direction=='right'){
                this.moveX = 0;
                this.moveY = 0;
                return 'popup_cover_right';
            }else if(direction=='top'){
                return 'popup_cover_top';
            }else if(direction=='bottom'){
                return 'popup_cover_bottom';
            }
        }
    }
}
</script>

<style scoped >
.yc_popup_cover{
    position:fixed;
    z-index:500;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background:rgba(0,0,0,.7);
    opacity: 1;
}
.popup_cover{
  position: fixed;
  z-index:502;
  background-color: #fff;
}
.popup_cont{
    position: absolute;
    overflow: hidden;
    background-color: #fff;
}
.popup_cover_bottom{
    bottom: 0;
    left:0;
    width: 100%;
    height:50%;
    overflow-y: scroll;
}
.popup_cover_top{
    top: 0;
    left:0;
    width: 100%;
    height:50%;
    overflow-y: scroll;
}
.popup_cover_left{
    left:0;
    top:0;
    height: 100%;
    width:50%;
    overflow-x: scroll;
}
.popup_cover_right{
    right:0;
    top:0;
    height:100%;
    width:50%;
    overflow-y: scroll;
}
/* // 动画 */
.yc_cover_fade-enter-active,.yc_cover_fade-leave-active {
    transition: opacity .5s ;
}
.yc_cover_fade-enter,.yc_cover_fade-leave-to{
    opacity: 0;
}
.popup_cover_left-enter-active,.popup_cover_left-leave-active {
    transition: transform .3s ;
}
.popup_cover_left-enter,.popup_cover_left-leave-to{
    transform: translateX(-100%);
}
.popup_cover_right-enter-active,.popup_cover_right-leave-active {
    transition: transform .3s ;
}
.popup_cover_right-enter,.popup_cover_right-leave-to{
    transform: translateX(100%);
}
.popup_cover_top-enter-active,.popup_cover_top-leave-active {
    transition: transform .3s ;
}
.popup_cover_top-enter,.popup_cover_top-leave-to{
    transform: translateY(-100%);
}
.popup_cover_bottom-enter-active,.popup_cover_bottom-leave-active {
    transition: transform .3s ;
}
.popup_cover_bottom-enter,.popup_cover_bottom-leave-to{
    transform: translateY(100%);
}
.yc_popup-leave-active{
    transition: transform .3s ease .6s;
}
</style>
