<template>
    <div class="yc_swipeCell" ref="yc_swipeCell" >
        <div class="yc_swipeCell_wrap" @touchstart="touchFn" :style="{transform:`translateX(${translateX}px)`,transitionDuration:`${translateTime}ms`}">
            <div class="yc_swipeCell_cont"  >
                <slot name="content" />
            </div>
            <div class="yc_swipeCell_right" ref="yc_swipeCell_right" v-on="$listeners">
                <slot name="right" />
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'yc_swipeCell',
    data(){
        return {
            swiperDom:null,
            slideDom:null,
            translateX:0,
            translateTime:0,
        }
    },
    mounted(){
        this.$nextTick(()=>{
            this.swiperDom = this.$refs.yc_swipeCell;
            this.slideDom = this.$refs.yc_swipeCell_right;
        })
    },
    methods:{
        touchFn(e){
            let that = this;
            let startX = e.changedTouches[0].clientX;
            let originStartX = e.changedTouches[0].clientX;
            let originStartY = e.changedTouches[0].clientY;
            let translateX = that.translateX;

            that.translateTime = 0;
            this.swiperDom.ontouchmove = function(e){
                let moveX = e.changedTouches[0].clientX;
                let originMoveX = e.changedTouches[0].clientX;
                let originMoveY = e.changedTouches[0].clientY;
                let changeX = moveX - startX;

                //防止误触，太灵敏
                if(originMoveX-originStartX<0 && originMoveX-originStartX > -5){
                    return;
                }
                if(Math.abs(originMoveX-originStartX) >= Math.abs(originMoveY-originStartY)){  //不影响上下滑动
                    changeX = translateX + changeX ;
                    changeX = Math.max(changeX,-that.slideDom.offsetWidth);
                    changeX = Math.min(0,changeX);
                    that.translateX = changeX;

                    return ;//阻止冒泡，捕获
                }
            }
            this.swiperDom.ontouchend = function(){
                that.translateTime = 600;
                if(that.translateX <= -that.slideDom.offsetWidth/2){
                    that.translateX = -that.slideDom.offsetWidth;
                }else{
                    that.translateX = 0;
                }
            }
        },
    }
}

</script>

<style scoped >
.yc_swipeCell{
    position:relative;
    overflow: hidden;
    width:100%;
}
.yc_swipeCell_wrap{
    width:100%;
}
.yc_swipeCell_cont{
    width:100%;
    background-color: #fff;
    min-height:140px;
}
.yc_swipeCell_right{
    position:absolute;
    left:100%;
    top:0;
    min-width:100px;
    height:100%;
}
</style>
