<template>
  <div class="yc_grid">
      <slot />
  </div>
</template>

<script>
export default {
    name: 'yc_grid',
    props: {
        column:{
            type: Number,
            default:3
        }
    },
    data(){
        return {
            itemWidth:{itemWidth:0}
        }
    },
    provide(){
        return{itemWidth:this.itemWidth}
    },
    mounted(){
        this.$nextTick(()=>{
            var dom = this.$children;
            var gridItemW = (100/this.column)+"%";
            this.itemWidth.itemWidth = gridItemW;
        })
    }
}


</script>

<style scoped >
.yc_grid{
    display:flex;
    flex-wrap: wrap;
    overflow: hidden;
}
</style>
