<template>
  <div class="yc_txtSlide" ref="yc_txtSlide">
        <div class="txtSlide_lis" v-if="direction=='row1'">
            <span class="yc_contentRow" ref="yc_contentRow">{{content}}</span>
        </div>
        <!-- 大盒子,高为一个txtSlide_wrap -->
        <div class="txtSlide_uls" ref="txtSlide_uls" v-if="direction=='column1'">
            <!-- <div class="txtSlide_wrap" ref="txtSlide_wrap"> -->
            <div class="txtSlide_lis" ref="txtSlide_lis" v-for="(val,idx) in txtColumn" :key="idx">
                <span class="yc_contentRow" ref="yc_contentRow">{{val}}</span>
            </div>
        </div>
  </div>
</template>

<script>
export default {
    name: 'yc_txtSlide',
    props: {
        direction:{
           type:String,
        },
        slideTime:{
           type:Number,
           default:1000,
        },
        content:{
            type:String | Array,
        }
    },
    data(){
        return {
           slideTimer:null,//记录定时器，组件销毁时清除定时器
           txtColumn:this.content,
        }
    },
    mounted(){
        var textSlide = this.$refs.yc_txtSlide;
        var direction = this.direction;
        if(direction=='row1'){
            this.slideRow(textSlide);
        }else if(direction=='column1'){
            this.slideColumn(textSlide);
        }
    },
    methods:{
        slideRow:function(textSlide){
            if(!textSlide){
               return;
            }
            var content = this.$refs.yc_contentRow;
            if(content){
                this.slideTimer = setInterval(() => {
                   var txt = content.innerText;
                   var temp = txt.charAt(0);
                   var len = txt.length;

                   content.innerText = txt.slice(1,len) + temp;
                },this.slideTime);
            }
        },
        slideColumn:function(textSlide){
            if(!textSlide){
               return;
            }
            this.slideTimer = setInterval(() => {
                    var arr = this.txtColumn;
                    var temp = arr.shift();
                    arr.push(temp);
                },this.slideTime);
        }
    },
    destroyed(){
        this.slideTimer = null;
    }
}


</script>

<style scoped >
.yc_txtSlide{
    overflow: hidden;
}
.txtSlide_uls{
    overflow:hidden;
}
.txtSlide_lis{
    width:1000%; 
}
.yc_contentRow{
    height:100%;
}
</style>
