<template>
    <div class="yc_list" ref="yc_wrap" :style="{height:height}"  @scroll.prevent.stop="addEventScroll">
        <div 
            class="yc_list_li" 
            ref="yc_content" 
            :style="{transform:`translate3d(0px,${translateY}px,0px)`}"
        >
            <div class="listWrap_vm_li" ref="listWrap_vm_li" v-show="false"></div>
            <div class="listWrap_vm_li" v-for="(val,idx) in showListData" :key="idx">
                数据{{val}}
            </div>
            <div class="yc_list_tip" v-if="isShowTip">
                请求数据中
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"yc_list",
    data(){
        return{
            showListData:[], //列表当前显示的数据
            showListNum:20,  //列表同时可显示数据，多少条
            translateY:0,
            listData:[],   //列表数据

            timer:new Date(),
            isShowTip:false,
            wrapDom:0,
            contDom:0,
        }
    },
    props:{
        height:{
            type:String,
            default:'300px'
        }
    },
    mounted(){
        this.wrapDom = this.$refs.yc_wrap;
        this.contDom = this.$refs.yc_content;
        this.getData();
    },
    methods:{
        getData(){
            let that = this;
            setTimeout(function(){
                let arr = [];
                for(var i=0;i<20;i++){
                    arr.push(i);
                }
                that.listData.push(...arr);
                if(!that.showListData.length){
                    that.showListData = arr.slice(0,20);
                }
                //页面初始化
                if(!that.showListData.length){
                    that.init();
                }
                that.isShowTip = false;
            },2000)
        },
        init(){
            this.showListData = [...that.listData];
        },
        addEventScroll(){
            // let that = this;
            // let listData = this.listData;
            //  let liHeight = that.$refs.listWrap_vm_li.offsetHeight;
            // let count = Math.ceil(that.wrapHeight/liHeight)*2;
            if(new Date()-this.timer <= 10 || this.isShowTip){
                return;
            }
            this.timer = new Date();

            let scrollTop = this.wrapDom.scrollTop;
            // console.log(124,scrollTop+this.contDom  this.wrapDom)
            if(scrollTop+this.contDom.offsetHeight >= this.wrapDom.offsetHeight){ //拉到底部
                this.isShowTip = true;
                this.getData();
            }

            return;
            if(wrapDom){
                wrapDom.addEventListener('touchstart',function(e){
                    let scrollYStart = this.scrollTop; 
                    wrapDom.ontouchmove = function(){
                        // 节流
                        if(new Date()-that.timer <= 1 || that.isShowTip){
                            return;
                        }
                        that.timer = new Date();

                        // let scrollY = this.scrollTop; 
                        // let startIdx = Math.floor(scrollY / liHeight);
                        // let endIdx = startIdx + count;

                        //可渲染的数据
                        // let showListData = that.listData.slice(startIdx, endIdx);
                        // that.showListData = showListData;
                        //margin-top
                        // that.translateY = startIdx * liHeight;
                        //判断是否拉到底部
                        // let liWrapDomH = liWrapDom.offsetHeight;
                        // return;
                        
                        if(showListData[showListData.length-1]==listData[listData.length-1] && scrollY>scrollYStart){
                            that.isShowTip = true;
                            scrollYStart = scrollY;//按住不放，更新
                            that.getData();
                        }
                    }
                })
            }
        }
    }
}
</script>
<style>
.yc_list{
    overflow-y:scroll ;
    border:1px solid #333;
}
.listWrap_vm_li{
    text-align:center;
    line-height: 100px;
    border-bottom: 1px solid grey;
}
.yc_list_tip{
    height:30px;
    background-color:pink;
}
</style>