<template>
  <div class="pullRefresh">
    <div class="pullRefresh_wrap" :style="{transitionDuration:durationTime +'ms',transform:`translate3d(0,${tipHeightCurrent}px,0)`}">
      <div class="pullRefresh_head"  @touchmove.stop.prevent>
        <div class="pullRefresh_head_state" v-if="pullState==1">
          <p>下拉即可刷新...</p>
        </div>
        <div class="pullRefresh_head_state" v-else-if="pullState==2">
          <p>释放即可刷新...</p>
        </div>
        <div class="pullRefresh_head_state pullRefresh_head_loading" v-else-if="pullState==3">
          <div class="pullRefresh_loading_icon" title="0">
            <svg version="1.1" id="loader-1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
            width="40px" height="40px" viewBox="0 0 40 40" enable-background="new 0 0 40 40" xml:space="preserve">
              <path opacity="0.2" fill="#000" d="M20.201,5.169c-8.254,0-14.946,6.692-14.946,14.946c0,8.255,6.692,14.946,14.946,14.946
              s14.946-6.691,14.946-14.946C35.146,11.861,28.455,5.169,20.201,5.169z M20.201,31.749c-6.425,0-11.634-5.208-11.634-11.634
              c0-6.425,5.209-11.634,11.634-11.634c6.425,0,11.633,5.209,11.633,11.634C31.834,26.541,26.626,31.749,20.201,31.749z"/>
              <path fill="#000" d="M26.013,10.047l1.654-2.866c-2.198-1.272-4.743-2.012-7.466-2.012h0v3.312h0
              C22.32,8.481,24.301,9.057,26.013,10.047z">
              <animateTransform attributeType="xml"
                attributeName="transform"
                type="rotate"
                from="0 20 20"
                to="360 20 20"
                dur="1s"
                repeatCount="indefinite"/>
              </path>
            </svg>
          </div>
          <p class="pullRefresh_loading_p">加载中</p>
        </div>
      </div>
      <div class="pullRefresh_cont" 
          ref="pullRefresh_cont"
          @touchstart="pullStart">
        <slot></slot> 
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'yc_pullRefresh',
  data(){
    return{
      pullState:1,//1下拉即可刷新 样式，2释放即可刷新 样式，3加载中样式
      isLoadingFresh: false,
      isFreshEnd:true,
      pullStartY:0,
      durationTime:0,
      tipHeightCurrent:0,
  }
  },
  props:{
    isLoading:{
      type:Boolean,
      default:false
    }
  },
  watch:{
    isLoading:function(newVal){
      let that = this;
      if(!newVal){
        this.durationTime = 300;
        this.tipHeightCurrent = 0;
        setTimeout(function(){  //防止 加载中 向 默认状态过度 时下拉
          that.isLoadingFresh = false;
          that.isFreshEnd = true;
          that.pullStartY = 0;
        },1000);
      }
    }
  },
  methods:{
    pullStart(e){
      let that = this;
      if(this.isLoadingFresh){
        return;
      }
      this.durationTime = 0;
      this.pullStartY = e.changedTouches[0].clientY;
      let pullDom = this.$refs.pullRefresh_cont;
      
      pullDom.ontouchmove = function(e){
        let pullMoveY = e.changedTouches[0].clientY;
        let changeY = pullMoveY-that.pullStartY;
        that.pullMoveY = pullMoveY;

        if(changeY>0 ){ //临时，暂时只支持下拉刷新
          that.tipHeightCurrent = Math.min(changeY,50);
 
          if(changeY<50){ 
            that.pullState = 1;
          }else if(changeY>=50){
            that.pullState = 2;
            that.isLoadingFresh = true;
            that.isFreshEnd = false;
          }
          return false;
        }
      }
      pullDom.ontouchend = function(){
        //下拉即可刷新阶段
        if(that.pullState==1){
          that.durationTime = 300;
          that.tipHeightCurrent = 0;
          setTimeout(function(){  
            that.pullStartY = 0;
          },1000);
        }
        //请求数据阶段
        if(!that.isFreshEnd && that.pullMoveY-that.pullStartY>=0){
          that.pullState = 3;
          that.$emit('onRefresh');
          that.isFreshEnd = true;
        }
        this.ontouchmove = null;
        this.ontouchend = null;
      }
    }
  }
}
</script>

<style>
.pullRefresh{
  overflow: hidden;
  height:100%;
  overflow-y:scroll;
}
.pullRefresh_wrap{
  position:relative;
}
.pullRefresh_loading_icon{
  display: flex;
}
.pullRefresh_head{
  position: absolute;
  transform: translateY(-100%);
  width: 100%;
  height: 80px;
}
.pullRefresh_head_state{
  display:flex;
  justify-content: center;
  align-items:center;
  height:80px;
  line-height:80px;
  color:#646566;
  font-size:20px;
}
.pullRefresh_loading_icon{
  padding-right:10px;
}
</style>