<template>
    <div class="yc_chart_circle" :style="{width:width,height:width}">
        <!-- 最外层圆 -->
        <div class="yc_chart_circle_wrap" :style="{'backgroundColor':checkColor}">
            <!-- 内层有颜色的圆 -->
            <div class="yc_chart_wrap_left" :style="{'backgroundColor':noColor,'transform': 'rotate('+degLeft+'deg)'}"></div>
            <div class="yc_chart_wrap_right" :style="{'backgroundColor':percent<=50 ? noColor :checkColor ,'width':degRight==0?'51%':'50%','transform': 'rotate('+degRight+'deg)'}"></div>
            <!-- 内层显示内容 -->
            <div class="yc_chart_wrap_cont">{{cont}}</div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'yc_chart_circle',
    data(){
        return {
            degLeft:0,
            degRight:0,
        }
    },
    props:{
        width:{
            type:String,
            default:'30px',
        },
        cont:{
            type:String,
        },
        percent:{
            type:Number,
            default:0,
        },
        checkColor:{
            type:String,
            default:'pink',
        },
        noColor:{
            type:String,
            default:'black',
        }
    },
    created(){
      
    },
    mounted(){
      var percent = this.percent;
      if(percent<=50 && percent>=0){
        var deg = Math.floor(percent/100*360*10)/10;
        this.degRight=  deg;
      }else if(percent>50 && percent<=100){
        percent -=50;
        var deg = Math.floor(percent/100*360*10)/10;
        this.degLeft=  deg;
      }else{
        console.log("yc_chart_circle组件报错：percent值应该在0到100")
      }
    },
    methods:{
       
    }
}

</script>

<style scoped>
.yc_chart_circle{

}
.yc_chart_circle_wrap{
    position:relative;
    overflow:hidden;
    width:100%;
    height:100%;
    border-radius:50%;
    background-color: #DDE6FF;
}
.yc_chart_wrap_circle{
    position:absolute;
    z-index:10;
    width:100%;
    height:100%;
    border-radius:50%;
    background-color:pink;
    /* // transform: rotate(45deg); */
}
.yc_chart_wrap_cont{
    position:absolute;
    z-index:20;
    width:80%;
    height:80%;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    background-color:#fff;
    border-radius:50%;
    font-size:12px;
    display: flex;
    justify-content: center;
    align-items: center
}
.yc_chart_wrap_left{
    position:absolute;
    width:50%;
    height: 100%;
    left:0;
    z-index:12;
    transform-origin: center right;
}
.yc_chart_wrap_right{
    position:absolute;
    width:51%;
    height: 100%;
    right:0;
    z-index:13;
    transform-origin: center left;
}
</style>
