//targetName,最后传到哪个组件到名字,不写默认全局混入
function $dispatch(eventName,data,targetName){
    let parent = this.$parent;

    while(parent){
        parent.$emit(eventName,data);
        parent = parent.$parent;
    }
}

function boardcast(eventName,data,targetName){
    this.$children.forEach(child => {
        child.$emit(eventName,data);//子元素触发$emit
        if(child.$children.length){
            boardcast.call(child,eventName,data,targetName);
        }
    });
}

export default{
    methods:{
        $dispatch,
        $boardcast:function(eventName,data,targetName){
            boardcast.call(this,eventName,data);
        }
    }
}