<template>
    <div class="yc_cascader">
        <div class="yc_cascader_nav">
            <div class="yc_cascader_wrap"  @click="gotoAll">
                <span class="yc_cascader_wrap_span">全部</span>
            </div>
            <div class="yc_cascader_wrap" v-for="(val,idx) in cascader" :key="idx" @click="changeNav(idx)">
                <span class="yc_cascader_wrap_span">
                    <span class="yc_cascader_span_icon">></span>
                    {{val.text}}
                </span>
            </div>
        </div>
        <div class="yc_cascader_checked">
            <div class="yc_cascader_li" v-for="(val,idx) in conentCurrent" :key="idx" @click="changeCheck(val,idx)">
                <div class="yc_cascader_wrap">
                    <div :class="{'yc_cascader_wrap_icon':true,'yc_cascader_wrap_selected':checkedIdx==idx}"></div>
                </div>
                <div class="yc_box_cont">{{val.text}}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'yc_cascader',
    data(){
        return{
            conentCurrent:[],
            checkedIdx:-1,
        }
    },
    props:{
        options:{   //总数据
            type:Array,
            default:[]
        },
        cascader:{   //当前导航栏数据
            type:Array,
            default:[]
        }
    },
    model:{
        prop:"cascader",
        event:"changeCascader"
    },
    mounted(){
        this.$nextTick(function(){
            this.init();
        })
    },
    methods:{
        init(){
            var cascader = this.cascader;
            if(cascader.length){
                var len = cascader.length;

                if(cascader[len-1].children){
                    this.conentCurrent = cascader[len-1].children;
                    this.checkedIdx = -1;
                }else{
                    var searchObj = this.searchTree(this.options,cascader[len-1].id,{},{idx:-1,parent:{}},0);
                    this.checkedIdx = searchObj.idx;
                    this.conentCurrent = searchObj.parent.children;
                }
            }else{
                this.conentCurrent = this.options;
                this.checkedIdx = -1;
            }
        },
        changeCheck(val,idx){
            var cascader = this.cascader;

            if(this.checkedIdx==idx){ //取消
                this.cascader.pop();
                this.checkedIdx = -1;
            }else{    //确认
                var searchObj = this.searchTree(this.options,val.id,{},{idx:-1,parent:{}},0);

                if(cascader.length){    //不为空
                    var len = cascader.length;

                    if(cascader[len-1].id==searchObj.parent.id){
                        cascader.push(val);
                    }else{
                        cascader.splice(len-1,1,val);
                    }
                }else{                  //导航栏为空,可以认为不存在查不到情况，因为没有就不会点击到它 
                    cascader.push(val);
                }
                //处理内容区 显示数据
                let lenCurrent = cascader.length;
                if(cascader[lenCurrent-1].children){
                    this.conentCurrent = cascader[lenCurrent-1].children;
                }
                //处理checkedIdx
                if(val.children){
                    this.checkedIdx = -1;
                }else{
                    this.checkedIdx = idx;
                }
            }
        },
        searchTree(data,id,parent,res,idx){
            if(data.id==id){
                res.parent = parent;
                res.idx = idx;
                return res;
            }
            for(var i=0;i<data.length;i++){
                if(res.idx!=-1){
                    return res;
                }
                var tempData = data[i];
                if(tempData.id==id){
                    res.parent = parent;
                    res.idx = i;
                }else{
                    if(tempData.children){
                        res.parent = data[i];
                        this.searchTree(tempData.children,id,data[i],res,i);
                    }
                }
            }
            return res;
        },
        changeNav(idx){
            var cascader = this.cascader;
            var len = cascader.length;

            cascader.splice(idx+1,len);
            this.init();
        },
        gotoAll(){
            var len = this.cascader.length;
            this.cascader.splice(0,len);
            this.init();
        }
    }
}
</script>

