<template>
  <div class="pickertime">
      <van-popup v-model="isShowCalendar"  position="bottom" ref="PickerTime">
            <van-picker
                title="选择时间"
                show-toolbar
                :columns="columnsData"
                @change="chagePicker"
                @cancel="cancel"
                @confirm="confirm"
            />
      </van-popup>
  </div>
</template>

<script>
export default {
    name:'pickertime',
    data(){
        return{
            dateVaule:"",
            isShowCalendar:true,
            columnsData:[],
            pickerArr:[],
        }
    },
    created(){
        //拖动月day数组重新计算
        let date = new Date();
        let columnsData=[];
        let pickerArr = [];

        let yearCurrent = date.getFullYear(); //年
        let yearObj =  {
            values:this.numberToArr(yearCurrent-2,yearCurrent+2),
            defaultIndex:2,
        }
        let monthObj = {        //月
            values:this.numberToArr(1,12,3),
            defaultIndex: date.getMonth()+12,
        }
        let currentMaxDay = new Date(date.getFullYear(), date.getMonth()+1, 0).getDate();
        let dayObj = {      //日
            values:this.numberToArr(1,currentMaxDay,3),
            defaultIndex:date.getDate()-1+currentMaxDay
        }
        let hourObj = {        //时
            values:this.numberToArr(0,23,3),
            defaultIndex:date.getHours()+24
        }
        let minuteObj = {        //分
            values:this.numberToArr(0,59,3),
            defaultIndex:date.getMinutes()+60
        }
        let secondObj = {         //秒
            values:this.numberToArr(0,59,3),
            defaultIndex:date.getSeconds()+60
        }
        columnsData.push(yearObj,monthObj,dayObj,hourObj,minuteObj,secondObj);
        pickerArr.push(date.getFullYear(),date.getMonth()+1,date.getDate(),date.getHours(),date.getMinutes(),date.getSeconds());
        this.columnsData = columnsData;
        this.pickerArr = pickerArr;
    },
    methods:{
        showCalendar(){
            this.isShowCalendar = true;
            document.activeElement.blur();
        },
        confirm(){
            //跟新日期字符串
            // let dateVaule = "";
            // this.pickerArr.map((val,idx)=>{
            //   dateVaule  += val+'/';
            // })
            // this.dateVaule = dateVaule;
            // this.isShowCalendar = false;
            this.$emit('comfirm',this.pickerArr);
        },
        cancel(){
            this.$emit('cancel');
        },
        numberToArr(start,end,count){
            var arr = [];
            var count = count || 1;

            for(var j=0;j<count;j++){
              for(var i=start;i<=end;i++){
                arr.push(i);
              }
            }
            return arr;
        },
        chagePicker(picker, value, index){
            //跟新 当前月多少天
            let date = new Date();
            let currentMaxDay = new Date(value[0], value[1], 0).getDate();
            if(index==0 || index==1){
                let dayObj = {  //日
                    values:this.numberToArr(1,currentMaxDay,3),
                    defaultIndex:Math.min(this.pickerArr[2]-1,currentMaxDay-1)+currentMaxDay //是数组下标要减1
                }
                this.$set(this.columnsData,2,dayObj);
                value[2] = Math.min(this.pickerArr[2],currentMaxDay); //是具体值，直接取
            }

            let valuesLen = this.columnsData[index].values.length;
            let currentColIdx = picker.getIndexes()[index];
            //拉到底部 || 顶部
            if(currentColIdx >= valuesLen*2/3 || currentColIdx< valuesLen/3){
                if(index==1){
                    picker.setColumnIndex(index,value[index]+12-1);
                }else if(index==2){
                    picker.setColumnIndex(index,value[index]+currentMaxDay-1);
                }else if(index==3){
                    picker.setColumnIndex(index,value[index]+24);
                }else if(index==4 || index==5){
                    picker.setColumnIndex(index,value[index]+60);
                }
            }
            this.pickerArr = value;
        },
    }
}
</script>
<style scoped>
.pickertime .van-overlay{
    z-index:9008 !important;
}
.pickertime .van-popup{
    z-index:9009 !important;
}
</style>>
