<template>
    <div class="yc_checkbox_group">
        <slot />
    </div>
</template>

<script>
export default {
    name: 'yc_checkbox_group',
    props: {
        result:{
            type:Array,
            default:[]
        },
        multiple:{
            type:Boolean,
            default:true
        },
        afeter_selected:{
            type:Function,
            default:()=>{}
        }
    },
    model:{
        prop:'result',
        event:'changeResult'
    },
    mounted(){
        this.$on('chageCheckedGroup',function(nameChecked,isSelected){
            var result = this.result;
            if(this.multiple){  //多选
                if(isSelected){
                    result.push(nameChecked);
                }else{
                var index = result.indexOf(nameChecked);
                result.splice(index,1);
                }
            }else{   //单选
                if(isSelected){
                    this.$set(result,0,nameChecked);
                }else{
                    result.pop();
                }
            }
            

            this.$emit('changeResult',result);
            this.afeter_selected();
        })
    }
}


</script>

