<template>
    <div class="yc_txtSlide" ref="yc_slider">
        <div class="txtSlide_wrap" ref="txtSlide_wrap" @touchstart="clickSlide($event)">
            <!-- 颜色区域 -->
            <div class="txtSlide_wrap_cont" ref="txtSlide_wrap_cont" :style="{'backgroundColor':bgColor,'width':contVal+'%','opacity':forbidden?0.5:1} ">
                <span 
                    class="txtSlide_wrap_cont_bar" 
                    ref="txtSlide_wrap_cont_bar" 
                    :style="{'backgroundColor':bgColor} "
                    @touchstart.prevent.stop="slideBar($event)"
                    @touchmove.prevent.stop
                >{{contVal}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'yc_slider',
    props: {
        value:{
           type:Number,
           default:0,
        },
        forbidden:{
            type:Boolean,
            default:false,
        },
        bgColor:{
            type:String,
            default:'#1989fa',
        }
    },
    data(){
        return {
            contVal:this.value,
            slideTimer:null,
        }
    },
    mounted(){
    },
    methods:{
        slideBar:function(e){
            if(this.forbidden){
                return;
            }
            var bar = this.$refs.txtSlide_wrap_cont_bar;
            var allWidth = this.$refs.txtSlide_wrap.offsetWidth; //进度条总长
            var nowWidth = this.$refs.txtSlide_wrap_cont.offsetWidth; //当前进度条长
            var temp = e.changedTouches[0].clientX;
            var that = this;
            var barW = bar.offsetWidth;

            bar.addEventListener('touchmove',function(e){
                var changeClientX = e.changedTouches[0].clientX-temp;
                var contVal = Math.floor((changeClientX+nowWidth)*100/allWidth);

                if(contVal>99){
                    contVal=100;
                }else if(contVal<0){
                    contVal=0;
                }
                that.contVal = contVal;
            },{ passive: false })
        },
        clickSlide:function(e){
            if(this.forbidden){
                return;
            }
            var bar = this.$refs.txtSlide_wrap_cont_bar;
            var allWidth = Math.floor(this.$refs.txtSlide_wrap.offsetWidth); //进度条总长
            var contW = Math.floor(this.$refs.txtSlide_wrap_cont.offsetWidth); //当前进度条长
            var clickX = Math.floor(e.changedTouches[0].clientX);//点击的地方
            var that = this;

            var mark = 1;
            if(clickX<contW){
                mark=-1;
            }else if(clickX==contW){
                return;
            }
            if(!this.slideTimer){
                this.slideTimer = setInterval(function(e){
                    if(mark==1){ //点在右侧
                        contW+=4;
                        contW = Math.min(clickX,contW);
                    }else if(mark==-1){ //点在左侧
                        contW-=4;
                        contW = Math.max(clickX,contW);
                    }
                    that.contVal = Math.floor(contW*100/allWidth);
                    //到达点击点，注销定时器
                    if(contW==clickX){
                        clearInterval(that.slideTimer);
                        that.slideTimer = null;
                    }
                },8);
            }
        },
    }
}


</script>

<style scoped >
.txtSlide_wrap{
    position:relative;
    height:4px;
    background-color:#c7c8ca;
}
.txtSlide_wrap_cont{
    position:absolute;
    left:0;
    height:100%;
}
.txtSlide_wrap::before {
    position: absolute;
    top: -8px;
    right: 0;
    bottom: -8px;
    left: 0;
    content: '';
}
.txtSlide_wrap_cont_bar{
    float:right;
    transform: translate(50%,-50%);
    font-size: 24px;
    line-height: 30px;
    padding:4px 8px;
    text-align: center;
    border-radius: 14px;
}
</style>
