<template>
    <div class="yc_picker">
        <div class="yc_picker_control">
            <div class="yc_control_wrap" @click="cancelPicker">取消</div>
            <div class="yc_control_wrap yc_control_mid">{{$attrs.title}}</div>
            <div class="yc_control_wrap yc_control_confirm" @click="comfirmPicker">确认</div>
        </div>
        <div class="yc_picker_cont" @touchmove.stop.prevent  ref="yc_picker_cont">
            <div class="yc_cont_wrap" v-for="(valObj,idxObj) in columns" 
                :key="idxObj" >
                <ul class="yc_cont_ul"  
                    ref="yc_cont_ul"
                    @touchstart="pullStart($event,idxObj)"
                    :style="{padding:`${emptyLiNum*liHeight}px 0`,transitionDuration:durationTime[idxObj] +'ms',transform:`translate3d(0,${moveCurrent[idxObj]}px,0)`}"
                >
                    <li :class="{'yc_cont_li':true,'yc_cont_checkedLi':checkedColumn[idxObj]==idx}" v-for="(val,idx) in valObj.values" :key="idx" ref="yc_picker_li">{{val}}</li>
                </ul> 
            </div>
            <div class="yc_cont_mask" :style="{backgroundSize:`100% ${emptyLiNum*liHeight}px` }"></div> 
            <div class="yc_cont_checked"></div> 
        </div>
    </div>
</template>

<script>
export default {
    name:'yc_picker',
    data(){
        return{
            checkedColumn:[],
            durationTime:[],
            moveCurrent:[],
            liHeight:0,
            emptyLiNum:0, //有几个空li标签
            showLiNum:5, //每列，同时可见多少行
            isMoveing:false,
        }
    },
    props:{
        columns:{
            type:Array,
            default:[]
        },
        cancel:{
            type:Function,
            default:()=>{}
        },
        comfirm:{
            type:Function,
            default:()=>{}
        },
        change:{
            type:Function,
            default:()=>{}
        }
    },
    mounted(){
        this.$nextTick(()=>{
            var liDom = this.$refs.yc_picker_li;
            this.liHeight = liDom[0].offsetHeight;
            
            var len = this.columns.length;
            //滑动时间
            this.durationTime = new Array(len).fill(200);
            //每列当前translateY值
            this.emptyLiNum = Math.floor(this.showLiNum/2);
            //初始化moveCurrent ,checkedColumn
            for(var i=0;i<this.columns.length;i++){
                var num = this.columns[i].defaultIndex;
                var len = this.columns[i].values.length;

                this.moveCurrent[i] = -this.columns[i].defaultIndex * this.liHeight;
                this.checkedColumn[i] = this.columns[i].defaultIndex;
            }
        })
    },
    methods:{
        pullStart(e,idx){
            let that = this;
            let pullStartY = e.changedTouches[0].clientY;
            let pullDom = this.$refs.yc_cont_ul;
            
            pullDom[idx].ontouchmove = function(e){
                let pullMoveY = e.changedTouches[0].clientY;
                let changeY = pullStartY - pullMoveY;
                pullStartY = pullMoveY;//更新pullStartY

                if(Math.abs(changeY) > that.liHeight){
                    that.$set(that.durationTime,idx,1000);
                }

                let move = Math.min(that.moveCurrent[idx] - changeY , 0);
                let num = that.columns[idx].values.length - 1; 
                move = Math.max(-num*that.liHeight , move);
                that.$set(that.moveCurrent,idx,move);
            }
            pullDom[idx].ontouchend = function(){
                var num = Math.round(that.moveCurrent[idx]/that.liHeight);

                that.$set(that.durationTime,idx,200);
                that.$set(that.moveCurrent,idx,num * that.liHeight);
                if(Math.abs(num) != that.checkedColumn[idx]){
                    that.$set(that.checkedColumn,idx,Math.abs(num));
                    that.$emit('change',that.checkedColumn);
                }

                pullDom[idx].ontouchmove = null;
                pullDom[idx].ontouchend = null;
            }
        },
        cancelPicker(){
            this.$emit('cancel');
        },
        comfirmPicker(){
            this.$emit('comfirm',this.checkedColumn);
        }
    }
}
</script>

<style>
.yc_picker{
    overflow: hidden;
    background-color:#fff;
}
.yc_picker_control{
    position: relative;
    z-index: 4;
    background-color: #fff;
    display: flex;
    height:40px;
    font-size:28px;
    line-height:28px;
    justify-content: space-between;
    align-items: center;
    padding:20px;
}
.yc_control_mid{
    padding: 0 20px;
}
.yc_control_confirm{
    color:#576b95;
}
.yc_picker_cont{
    display:flex;
    position:relative;
    height:380px;
}
.yc_cont_wrap{
    flex: 1;
}
.yc_cont_ul{
    overflow: hidden;
    transition-timing-function: cubic-bezier(0.23, 1, 0.68, 1);
}
.yc_cont_li{
    font-size:28px;
    line-height:76px;
    height:76px;
    text-align: center;
    user-select: none;
}
.yc_cont_mask{
    position: absolute;
    width:100%;
    height: 100%;
    z-index:1;
    background-image: linear-gradient(180deg, hsla(0, 0%, 100%, 0.9), hsla(0, 0%, 100%, 0.4)), 
        linear-gradient( 0deg, hsla(0, 0%, 100%, 0.9), hsla(0, 0%, 100%, 0.4));
    background-repeat: no-repeat;
    background-position: top, bottom;
    transform: translateZ(0);
    pointer-events: none;
}
.yc_cont_checked{
    position: absolute;
    display: flex;
    width: 100%;
    height:76px;
    z-index: 2;
    top:50%;
    transform: translateY(-50%);
    pointer-events: none;
    border-top:1px solid #ebedf0;
    border-bottom: 1px solid #ebedf0;
}
.yc_cont_checkLi{
    flex:1;
}
.yc_cont_checkedLi{
    color:#000;
}
</style>