<template>
  <div class="yc_autograph"  :style="{width:widthParams,height:heightParams}" >
    <canvas id="yc_autograph_canvas" 
        @touchstart.prevent="start" 
        @touchmove.prevent="move" 
    />
    <!-- canvas标签上不要:style或者:width，绘制时位置不对 -->
    <div class="yc_autograph_contral">
        <span class="text" @click="cancel">清除</span>
        <span class="text" @click="comfirmFn">确定</span>
    </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            ctx:null,
            isMove:false,
        }
    },
    props:{
        widthParams:{
            type:String,
            default:'100%'
        },
        heightParams:{
            type:String,
            default:'40px'
        },
        comfirm:{
            type:Function,
            default:()=>{}
        }
    },
    mounted(){
        this.initCanvas();
    },
    methods:{
        initCanvas(){
            var canvas = document.getElementById("yc_autograph_canvas");
            var wrap = document.getElementsByClassName('yc_autograph')[0];

            if(canvas){
                canvas.width=wrap.offsetWidth;
                canvas.height=wrap.offsetHeight;

                var ctx = canvas.getContext('2d');
                this.ctx = ctx;
                ctx.lineCap = 'round';
                ctx.strokeStyle="black";
                ctx.lineWidth=5;
            }
        },
        start(e){
            var canvasLeft = e.target.offsetLeft;
            var canvasTop = e.target.offsetTop;
            var pageX = e.changedTouches[0].pageX;
            var pageY = e.changedTouches[0].pageY;

            this.ctx.beginPath();
            this.ctx.moveTo(pageX-canvasLeft,pageY-canvasTop);
        },
        move(e){
            if(this.isMove){
                return;
            }
            this.isMove = true;

            var canvasLeft = e.target.offsetLeft;
            var canvasTop = e.target.offsetTop;
            var pageX = e.changedTouches[0].pageX;
            var pageY = e.changedTouches[0].pageY;
     
            this.ctx.lineTo(pageX-canvasLeft,pageY-canvasTop);
            this.ctx.stroke();

            this.isMove = false;
        },
        cancel(){
            var canvas = document.getElementById("yc_autograph_canvas");
         
            this.ctx.clearRect(0,0,canvas.width,canvas.height);  
        },
        comfirmFn(){
            this.$emit('comfirm');
        }
    }
}
</script>

<style scoped>
#yc_autograph_canvas{
    background-color: grey;
}
.yc_autograph_contral{
    display:flex;
    justify-content:space-between;
}
.yc_autograph_contral .text{
    color:#1989fa;
    font-size:32px;
    line-height:42px;
    padding:10px 0;
}
</style>