<template>
    <div class="yc_checkbox" @click="checkFn">
        <div class="yc_box_icon">
            <div :class="{'yc_box_icon_circle':true,'yc_box_icon_selected':groupChecked}" v-if="name"></div>
            <div :class="{'yc_box_icon_circle':true,'yc_box_icon_selected':checked}" v-else></div>
        </div>
        <div class="yc_box_cont">
            <slot />
        </div>
    </div>
</template>

<script>
export default {
    name: 'yc_checkbox',
    data(){
        return {
            name:'',
            result:[],
            groupChecked:false
        }
    },
    props: {
        checked:{
            type:Boolean,
            default:false
        },
    },
    model:{
        prop:'checked',
        event:'changeChecked'
    },
    watch:{
        result:{
            handler:function(){
                this.$nextTick(()=>{
                    if(this.result.indexOf(this.name)===-1){
                        this.groupChecked = false;
                    }else{
                        this.groupChecked = true;
                    }
                })
            },
            deep:true
        }
    },
    mounted(){
        this.init();
    },
    methods:{
        init(){
            this.name = this.$attrs ? this.$attrs.name : '';
            this.result = this.$parent.result;
        },
        checkFn(){
            if(this.name){ //复选框组
                this.groupChecked = !this.groupChecked;
                this.$parent.$emit('chageCheckedGroup',this.name,this.groupChecked);
            }else{ // 单个 复选框
                this.$emit('changeChecked',!this.checked);
            }
        }
    }
}


</script>

<style scoped>
.yc_checkbox{
    display:flex;
    align-items:center;
}
.yc_box_icon{
    display: flex;
    width:40px;
    justify-content: center;
    align-items:center;
}
.yc_box_icon_circle{
    width:30px;
    height:30px;
    border-radius: 50%;
    border:1px solid #c8c9cc;
}
.yc_box_cont{
    font-size:30px;
    line-height:40px;
    padding-left:10px;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
    user-select: none;
}
.yc_box_icon_selected{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:#1989fa;
    border-color:#1989fa;
}
.yc_box_icon_selected::before {
    display: inline-block;
    content: '\2713';
    color:#fff;
}
</style>
