<template>
    <transition name="yc_overlay">
        <div class="yc_overlay" 
            v-if="show" 
            @click.stop.prevent="closePopup" 
            @touchmove.stop.prevent
            v-on="$listeners"
        >
            <slot />
        </div>
    </transition>
</template>

<script>
export default {
    name: 'yc_overlay',
    props: {
        show:{
            type:Boolean,
            default:false
        }, 
    },
    model:{
        prop:'show',
        event:'changeShow'
    },
    methods:{
        closePopup(){
            this.$emit('changeShow',false);
        }
    }
}
</script>

<style scoped >
.yc_overlay{
    position:fixed;
    z-index:500;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background:rgba(0,0,0,.7);
    opacity: 1;
}
.yc_overlay-enter-active,.yc_overlay-leave-active {
    transition: opacity .3s ;
}
.yc_overlay-enter,.yc_overlay-leave-to{
    opacity: 0;
}
</style>
