import Vue from 'vue';

export default function (Component,props){
  //1.创建component实例  new vue ；extend
  const vm = new Vue({
    render(h){
      return h(Component,props);//‘div’,{}属性，子节点由
    }
  }).$mount();

  //追加挂载dom
  document.body.appendChild(vm.$el);

  //销毁方法，可以关闭组件
  const comp = vm.$children[0];
  comp.remove=()=>{
    document.body.removeChild(vm.$el);
    vm.$destroy(); //只是取消注册事件，dom不会移除
  }

  return comp;
}
